import React from 'react';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import MakesModels from '../data/makesModels.json';

const styles = theme => ({
    root: {
      ...theme.mixins.gutters(),
      padding: theme.spacing.unit * 2
    },
  });

class Manufacturer extends React.Component {  
  state = {
    value: '',
    newOrPreowned: sessionStorage.getItem("newOrPreowned"),
    dieselOrGas: sessionStorage.getItem("dieselOrGas")
  };

  


  handleChange = event => {
    this.setState({ value: event.target.value });
  };

  render() {
    
    if (this.state.newOrPreowed === 'preowned') {
        
    }
    if (this.state.dieselOrGas === 'diesel') {
        
        var manufacturer = Object.keys(MakesModels.diesel).map((make, idx) => {
            return (
                <FormControlLabel
                key={idx}
                value={make}
                control={<Radio color="primary" />}
                label={make}
                labelPlacement="start"
                />
            )   
        })
    }
    console.log(Object.keys(MakesModels.diesel))
    const { classes } = this.props;
    return (
      <Paper className={classes.root} elevation={1}>
        <Typography component={'span'} variant="h5" component="h3">
            Select Manufacturer
        </Typography>
        
      
        <FormControl component="fieldset">
            <RadioGroup
            aria-label="position"
            name="position"
            value={this.state.value}
            onChange={this.handleChange}
            row
            >
            {manufacturer}
            {/* <FormControlLabel
                value="new"
                control={<Radio color="primary" />}
                label="New"
                labelPlacement="start"
            />          
            <FormControlLabel
                value="preowned"
                control={<Radio color="primary" />}
                label="Pre-Owned"
                labelPlacement="start"
            /> */}
            </RadioGroup>
        </FormControl>
      </Paper>
    );
  }
}
Manufacturer.propTypes = {
    classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(Manufacturer);